import React from 'react';
import { auth } from "./firebase";
import { signOut } from 'firebase/auth';
function Sidebar({ exportation, notes, onAddNote, activeNoteId, setActiveNote, save }) {

    const userSignOut = () => {
        signOut(auth).then(() => {
            console.log("GOT SIGNED OUD");
        })
      }

    return <div className="app-sidebar">
        <h2>Kofi</h2>
        <div className="app-sidebar-notes">
            <div className="app-sidebar-note-add" onClick={() => {
                onAddNote();
            }}>
                <p>+ Ajouter une page</p>
            </div>
            <div className="app-sidebar-note-add" onClick={() => {
                exportation();
            }}>
                <p>Exporter</p>
            </div>
            <div className="app-sidebar-note-add" onClick={() => {
                userSignOut();
            }}>
                <p>D√©connecter</p>
            </div>
            <div className="app-sidebar-note-add" onClick={() => {
                save();
            }}>
                <p>Sauvegarder</p>
            </div>
            {notes.map((note) => (
                <div id="addNote"
                    className={`app-sidebar-note ${note.id === activeNoteId && "active"}`}
                    onClick={() => setActiveNote(note.id)}>
                    <p>{note.title.slice(0, 15)}</p>
                </div>
            ))}
        </div>
    </div>;
}

export default Sidebar;