import React from 'react';
import Sidebar from '../Sidebar';
import "../App.css"
import uuid from 'react-uuid';
import Main from '../Components/Main';
import data from '../JSON/data.json'
import { getDownloadURL, ref } from 'firebase/storage';
import { storage } from '../firebase';
import { uploadBytes } from 'firebase/storage';

class Home extends React.Component {

    constructor(props) {
        super(props);
        this.obj = require("../JSON/data.json");
        this.fileRef = ref(storage, `users/${this.props.user.uid}.json`);
        this.state = {
          notes: [],
          activeNoteId: "0"
        };
    }

    saveNotes = () => {

      const object = this.state.notes;
      const fileName = "data.json";
      const json = JSON.stringify(object);

      const file = new File([json], fileName, {
          type: "application/json",
          lastModified: Date.now()
      });

      const fileRef = ref(storage, `users/${this.props.user.uid}/notes.json`)

      uploadBytes(fileRef, file).then(() => {
          console.log("UPLOADED");
      })
  }

    componentDidMount() {
      if (this.props.user) {
        const fileRef = ref(storage, `users/${this.props.user.uid}/notes.json`);
        getDownloadURL(fileRef).then((url) => {
          if(url) {
            fetch(url)
            .then(response => response.json())
            .then(dataa => {
              if(dataa) {
                this.setState({notes: dataa});
              }
            })
          }
        });
      }
    }

    getData = () => {
      const fileRef = ref(storage, `users/${this.props.user.uid}.json`);
      getDownloadURL(fileRef).then((url) => {
        if(url) {
          fetch(url)
          .then(response => response.json())
          .then(dataa => {
            if(dataa) {
              return dataa;
            }
          })
        }
      });
    }

    exportData = () => {
        const jsonString = `data:text/json;chatset=utf-8,${encodeURIComponent(
        JSON.stringify(this.state.notes)
        )}`;
        const link = document.createElement("a");
        link.href = jsonString;
        link.download = "data.json";
    
        link.click();
    }
    
    componentDidUpdate() {
    }

    onAddNote = (specificId) => {
        const randomUUID = uuid();
        const newNote = {
          id: specificId || randomUUID,
          title: "üìÑNouvelle page",
          contente: "SALUT",
          body: [
            {
                id: "0",
                content: '/help pour d√©couvrir',
                style: {
                  "textDecoration":"none",
                  "fontWeight": "normal",
                  "font-style": "normal"
                }
            }
        ]
        };
        if (this.state.notes.length === 0) {
          newNote.title = "üèòÔ∏è Bienvenue !";
          newNote.id = "0";
          this.setState({
            notes: [newNote],
            activeNoteId: newNote.id
          });
        } else {
          this.setState((prevState) => ({
            notes: [newNote, ...prevState.notes],
            activeNoteId: newNote.id
          }));
        }
        return newNote;
      };
    
      setActiveNote = (noteId) => {
        this.setState({ activeNoteId: noteId });
      };
    
      getActiveNote = () => {
        let activeNote = this.state.notes.find((note) => note.id === this.state.activeNoteId);
        if (!activeNote) {
          activeNote = this.onAddNote();
        }
        return activeNote;
      };

      render() {

        return (
    
          <div className="App">
            <Sidebar exportation={this.exportData} save={this.saveNotes} notes={this.state.notes} onAddNote={this.onAddNote} activeNoteId={this.state.activeNoteId} setActiveNote={this.setActiveNote} />
            <Main activeNote={this.getActiveNote()} setActiveNote={this.setActiveNote}/>
          </div>
        );
      }

}

export default Home;