import React, { useCallback } from 'react';
import '../Styles/Auth.css'
import { useState } from 'react';
import logo from '../Images/Auth/kofi-logo.png';
import { createUserWithEmailAndPassword } from 'firebase/auth';
import { auth, storage } from '../firebase';
import { ref, uploadBytesResumable, getDownloadURL, uploadBytes } from "firebase/storage";

export default function SignUp() {

    const initializeJson = (user) => {

        const object = [
            {
              "id": "0",
              "title": "üèòÔ∏è Bienvenue !",
              "contente": "SALUT",
              "body": [
                {
                  "id": 0,
                  "content": "/help",
                  "style": {
                    "textDecoration": "underline",
                    "fontWeight": "normal",
                    "fontStyle": "normal"
                  }
                }
              ]
            }
          ];
        const fileName = "data.json";
        const json = JSON.stringify(object);

        const file = new File([json], fileName, {
            type: "application/json",
            lastModified: Date.now()
        });

        const fileRef = ref(storage, `users/${user.uid}/notes.json`)

        uploadBytes(fileRef, file).then(() => {
            console.log("UPLOADED");
        })
    }

    const [error, setError] = useState("");

    const handleForm = async (e) => {
        let email = document.getElementById("email").value;
        let firstPassword = document.getElementById("firstPassword").value;
        let secondPassword = document.getElementById("secondPassword").value;

                if (email) {
                    if (firstPassword.length > 6) {
                        if (firstPassword === secondPassword) {
                            e.preventDefault();
                            createUserWithEmailAndPassword(auth, email, firstPassword).then((user) => {
                                initializeJson(user.user);
                            });
                        } else {
                            setError("Les mots de passes ne correspondent pas !");
                        }
                    } else {
                        setError("Le mot de passe est trop court !");
                    }
                } else {
                    setError("Aucun email n'a √©t√© saisi !");
            }
        }


    return (
        <div id="container">
            <div id="login-container">
                <div id="logo-name-container">
                    <img id="brand-logo" src={logo} />
                    <h1 id="brand-text">Kofi</h1>
                </div>

                <div id="form">

                    <div id="email-form">
                        <p>Adresse email</p>
                        <input placeholder='xxxx@xxxx.com' id="email"></input>
                    </div>

                    <div id="password-form">
                        <p>Mot de passe</p>
                        <input placeholder='**********' type="password" id="firstPassword"></input>
                    </div>

                    <div id="password-form-repeat">
                        <p>R√©p√©ter mot de passe</p>
                        <input placeholder='**********' type="password" id="secondPassword"></input>
                    </div>

                    

                    <button id="submit" onClick={handleForm}>Submit</button>

                    <p id="no-account" ><a href="/">D√©j√† un compte ? Se connecter</a></p>

                    <h1 id="error">{error}</h1>
                </div>
            </div>
        </div>
    );
}