import React from 'react';
import '../Styles/Auth.css'
import logo from '../Images/Auth/kofi-logo.png';
import {auth} from "../firebase";
import { signInWithEmailAndPassword } from 'firebase/auth';

export default function Auth({onUserLogin}) {

    const handleLogin = async () => {

        let email = document.getElementById("email").value;
        let password = document.getElementById("password").value;

        if (email && password) {
            try {
                const userCredential = await signInWithEmailAndPassword(auth, email, password);
                console.log('User logged in:', userCredential.user);
            } catch (error) {
                console.error('Error logging in:', error);
            }
        }
    }

    return (
        <div id="container">
            <div id="login-container">
                <div id="logo-name-container">
                    <img id="brand-logo" src={logo} />
                    <h1 id="brand-text">Kofi</h1>
                </div>

                <div id="form">
                    <div id="email-form">
                        <p>Adresse email</p>
                        <input id="email" placeholder='xxxx@xxxx.com'></input>
                    </div>

                    <div id="password-form">
                        <p>Mot de passe</p>
                        <input id="password" placeholder='**********' type="password"></input>
                        <p><a id="forgot-pass" href='https://google.com'>Mot de passe oubli√© ?</a></p>
                    </div>

                    <button id="submit" onClick={handleLogin}>Se connecter</button>

                    <a href="/signup"><p id="no-account">Pas de compte ? En cr√©er un</p></a>
                </div>
            </div>
        </div>
    );
}

