import React, {useEffect, useState} from 'react';
import { onAuthStateChanged, signOut } from 'firebase/auth';
import { auth } from "../firebase";
const Accueil = () => {

    const [authUser, setAuthUser] = useState(null);

    useEffect(() => {
        const listen = onAuthStateChanged(auth, (user) => {
            if (user) {
                setAuthUser(user);
            } else {
                setAuthUser(null);
            }
        });

        return () => {
            listen();
        }

    }, []);

    const userSignOut = () => {
        signOut(auth).then(() => {
            console.log("GOT SIGNED OUD");
        })
    }

    return (
        <div>{authUser ? <p>{`Signed in as ${authUser.email}`}<button onClick={userSignOut}>SIGN out</button></p> : <p>Signed out</p>}</div>
    )

};

export default Accueil;